import { SwipeableDrawer } from '@mui/material'
import React, { useState } from 'react'
import styled from 'styled-components'
import { IoIosMenu } from 'react-icons/io'

import ProfileBox from './ProfileBox'
import { useNavigate } from 'react-router-dom'
import _ from 'lodash'
import { blogLanguageMenu } from '../../store/staticData'

const HeaderBox = () => {
  const navigate = useNavigate()
  const [drawerOpen, setDrawerOpen] = useState(false)

  const returnMenu = (data) => {
    return _.map(data, (item) => (
      <h5 style={{ padding: 10 }} onClick={() => navigate(item.navi)}>
        - {item.menuName}
      </h5>
    ))
  }

  return (
    <HeaderWrap>
      <div onClick={() => setDrawerOpen(true)} style={{ padding: 15, cursor: 'pointer' }}>
        <IoIosMenu color="white" size={30} />
      </div>

      <SwipeableDrawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        onOpen={() => setDrawerOpen(true)}>
        <DrawerWrap>
          <div onClick={() => navigate('/')}>
            <ProfileBox />
          </div>
          <MenuList>
            <h2>ABOUT ME</h2>
            <h2 onClick={() => navigate()}>BLOG</h2>
            {returnMenu(blogLanguageMenu)}
            <h2>PROJECT</h2>
          </MenuList>
        </DrawerWrap>
      </SwipeableDrawer>
    </HeaderWrap>
  )
}

export default HeaderBox

const HeaderWrap = styled.div`
  background-color: black;
  height: 50px;
  display: flex;
  align-items: center;
`

const DrawerWrap = styled.div`
  width: 200px;
`

const MenuList = styled.div`
  padding: 30px;

  h2 {
    margin: 20px 0;
    font-weight: 600;
    cursor: pointer;
    &:hover {
      color: green;
    }
  }
`
